// Header burger-menu

.hamburger__menu {
	display: none;
	position: relative;
	flex-grow: 1;
	width: 40px;
	justify-content: flex-end;
	padding: 15px;
	box-sizing: border-box;
}

.header-menu__btn {
	z-index: 100;
	position: relative;
	width: 30px;
	height: 25px;
	cursor: pointer;
}

.menu-btn {
	position: absolute;
  	width: 30px;
	height: 25px;
	box-sizing: border-box;
}

.menu-btn span,
.menu-btn::before,
.menu-btn::after {
  display: block;
  position: absolute;
  width: 30px;
  height: 2px;
  background: #1D1F20;
  -webkit-transition: all .2s ease-in-out;
  -moz-transition: all .2s ease-in-out;
  -ms-transition: all .2s ease-in-out;
  -o-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out;
}


.menu-btn span {
  top: 45%;
}

.menu-btn::before,
.menu-btn::after {
  content: "";
}

.menu-btn::before {
  top: 0px;
}

.menu-btn::after {
  bottom: 0px;
}

.menu-btn.btn-animation.close span {
  opacity: 0;
}

.menu-btn.btn-animation.close::before {
  top: 11px;
  -moz-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}

.menu-btn.btn-animation.close::after {
  bottom: 12px;
  -moz-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
}



.overflow-hidden {
    overflow: hidden;
}

.header-menu__btn {
	z-index: 200 ; 
}

.header-menu__nav {
	z-index: 20;
	display: none;
	position: fixed;
	top: 0;
	right: 0;
	width: 100vw;
	height: 100vh;
	background-color: $white;
	z-index: 150;
	overflow: hidden;
	transition-duration: 0.4s;
}

.nav_content {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.nav_list {
	width: 100%;
	margin-top: 80px;
	margin-bottom: 10px;
	padding-left: 0;
}

.nav_list-item {
	list-style: none;
}

.hover-target {
	display: inline-block;
	width: 100%;
	font-weight: 700;
	padding: 10px;
	text-decoration: none;
	box-sizing: border-box;
	overflow: hidden;
	transition-duration: 0.2s;
	font-size: 22px;
	text-align: center;
	color: $black;
	text-transform: uppercase;
	&:hover {
		background-color: $dark-blue;
		color: $white;
		text-decoration: none;
	}
}

.open {
	display: block;
}

.nav_contacts {
	display: flex;
	flex-direction: column;
	align-items: center;

	width: 100%;
	padding-bottom: 20px;
	padding-top: 20px;
}

.nav_phones {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: center;
	width: inherit;
	margin-bottom: 20px;
	font-size: 12px;
	img {
		display: none;
	}
}

.nav__phone {
	margin-right: 0;
	a {
		font-size: 24px;
	}
}

.nav-contacts_btn {
	justify-content: flex-start;
}

.nav-contacts_btn button {
	width: 260px;
	height: 55px;
	font-size: 14px;
}









//Adaptive version

@media (max-width: 1024px) {
	.hover-target {
		font-size: 30px;
		padding: 20px;
	}



	.hamburger__menu {
		display: flex;
	}


}

@media (max-width: 992px) {
	


}

@media (max-width: 768px) {

	.hover-target {
		padding-top: 20px;
		padding-bottom: 20px;
	}

}

@media (max-width: 640px) {

	.hover-target {
		font-size: 20px;
		padding: 12px;
	}

	.nav_phones .first-phone-number, .nav_phones .second-phone-number {
		padding: 10px;
		a {
			font-size: 20px;
		}
		span {
			font-size: 18px;
		}
	}

}

@media (max-width: 480px) {

	.hover-target {
		font-size: 16px;
		padding-top: 8px;
		padding-bottom: 8px;
	}

	.nav__phone {
		a {
			font-size: 20px;
		}
	}

	.nav_contacts {
		padding-top: 10px;
		padding-bottom: 10px;
	}

}

