//PRODUCT

.product__container {
	background-color: #FAFAFA;
}

.product__switch {
	display: flex;
	justify-content: center;
	margin-bottom: 30px;
	margin-top: 20px;
}

.product__btn-wrap {
	display: flex;
	justify-content: center;
	margin: 0 auto;
}

.switch__btn {
	display: flex;
	flex-direction: row;
	align-items: center;
	padding: 10px 30px;
	width: 200px;
	height: 55px;
	border: 1px dashed #4A45BE;
	svg {
		fill: $blue;
		margin-right: 10px;
	}
	span {
		color: $blue;
	}
	&:hover {
		background-color: $blue;
		border: none;
		cursor: pointer;
		svg {
			fill: $white;
		}
		span {
			color: $white;
		}
	}
}

.switch__btn-left {
	border-radius: 5px 0 0 5px;
}

.switch__btn-right {
	border-radius: 0 5px 5px 0;
}

.switch__btn--active {
	background-color: $blue;
	svg {
		fill: $white;
	}
	span {
		color: $white;
	}
}

.product__cards {
	display: flex;
	flex-direction: row;
}

.cards__left {
	display: flex;
	flex-direction: row;
}

.cards__right {
	display: flex;
	flex-direction: row;
}

.product__card {
	box-sizing: border-box;
	margin: 10px;
	border-radius: 10px;
	background-color: $white;
}

.card__pic {
	display: flex;
	justify-content: center;
	padding: 40px 40px 15px 40px;
	img {
		width: 70px;
		height: auto;
	}
}


.card__text {
	padding: 15px 15px 30px 15px;
}

.card__title {
	p {
		font-size: 16px;
		color: rgb(30, 24, 27);
		font-weight: bold;
		margin-bottom: 10px;
	}
}

.card__description {
	span {
		font-size: 14px;
		font-family: "Roboto";
		color: rgb(111, 111, 111);
	}
}

.card__price {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	padding: 20px 15px;
	border-top: 2px solid #F8F8F8;
}

.price__money {
	margin-right: 10px;
	p {
		font-size: 18px;
		font-family: "Gotham Pro";
		color: rgb(30, 24, 27);
		font-weight: bold;
	}
}

.price__btn {
	a {
		display: inline-block;
		padding: 8px 30px;
		font-size: 14px;
		font-family: "Gotham Pro";
		font-weight: bold;
		text-transform: uppercase;
		border-width: 2px;
		border: 2px solid $blue;
		border-radius: 5px;
		text-align: center;
		color: $blue;
		transition-duration: 0.3s;
		&:hover {
			background-color: $blue;
			color: $white;
			cursor: pointer;
		}
	}
}

// ----------




//Adaptive version

@media (max-width: 1400px) { //Empty
	
}

@media (max-width: 1200px) {

	.card__text {
		text-align: center;
	}

	.card__price {
		flex-direction: column;
	}
	.price__money {
		margin-bottom: 15px;
	}

}


@media (max-width: 1024px) { //Empty



}

@media (max-width: 992px) { 

	.product__cards {
		flex-wrap: wrap;
	}

}

@media (max-width: 768px) { 

	.product__cards {
		flex-direction: column;
	}

}

@media (max-width: 480px) {

	.cards__left,
	.cards__right {
		flex-direction: column;
	}

	.switch__btn-right {
		display: none;
	}
}


@media (max-width: 320px) { //Empty

}