//REVIEW

/*BLOCK -----------*/

.review__container {
	background-color: #FBFBFB;
}

.review__block {
	display: flex;
}

.review__item {
	position: relative;
	display: flex;
	flex-direction: column;
	justify-content: flex-end;
	background-color: $white;
	flex: 1;
	height: auto;
	padding: 30px 45px;
	border-radius: 10px;
	margin: 10px;
	border-radius: 10px;
	box-shadow: 0px 0 25px rgb(231, 230, 255);
	&:after {
		position: absolute;
		bottom: 20px;
		right: 20px;
		content: '';
		background: url('./../img/ic-quote.png') no-repeat;
		width: 50px;
		height: 36px;
	}
}

.review__text {
	margin-bottom: 10px;
	p {
		font-size: 14px;
		font-style: italic;
		line-height: 1.5;
	}
}

.review__person {
	display: flex;
	flex-direction: row;
	align-items: center;
}

.person__avatar {
	width: 40px;
	height: 40px;
	margin-right: 20px;
	img {
		width: 40px;
		height: 40px;
		border-radius: 50%;
	}
}


.person__name {
	margin-right: 20px;

}

.person__star {

	img {
		margin-right: 5px;
	}
}

