.modal-wrap {
	display: none;
	justify-content: center;
	align-items: center;
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	width: 100%;
	height: 100%;
	z-index: 1000;
	overflow: auto;
	background: rgba(51, 47, 109, 0.7);
}

.modal-open {
	display: flex;
}

.modal-form {
	position: relative;
	max-width: 300px;
	height: 400px;
	border-radius: 10px;
	padding-left: 30px;
	padding-right: 30px;
	z-index: 1001;
}

.modal-close {
	position: absolute;
	top: 10px;
	right: 10px;
	font-size: 32px;
	transform: rotate(45deg);
	cursor: pointer;
	color: $white;
}


.modal-title {
	p {
		font-size: 22px;
	}
}

.success-container {
	display: flex;
	flex-direction: column;
	align-items: center;
	margin: 0;
}

.success-form {
	height: 300px;
}

.success-title {
	margin: 0;
}

.success-img {
	width: 100px;
	margin-bottom: 20px;
}