.menu-btn {
  position: absolute;
  width: 30px;
  height: 25px;
  box-sizing: border-box; }

.menu-btn span,
.menu-btn::before,
.menu-btn::after {
  display: block;
  position: absolute;
  width: 30px;
  height: 2px;
  background: #1D1F20;
  -webkit-transition: all .2s ease-in-out;
  -moz-transition: all .2s ease-in-out;
  -ms-transition: all .2s ease-in-out;
  -o-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out; }

.menu-btn span {
  top: 45%; }

.menu-btn::before,
.menu-btn::after {
  content: ""; }

.menu-btn::before {
  top: 0px; }

.menu-btn::after {
  bottom: 0px; }

.menu-btn.btn-animation.close span {
  opacity: 0; }

.menu-btn.btn-animation.close::before {
  top: 11px;
  -moz-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg); }

.menu-btn.btn-animation.close::after {
  bottom: 12px;
  -moz-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg); }

