//FOOTER

/*BLOCK -----------*/

.footer__container {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	background-color: #5A53BA;
}

.footer__wrap {
	width: 100%;
}

.footer__contacts {
	display: flex;
	flex-direction: row;
	padding-top: 40px;
	padding-bottom: 40px;
}

.contacts__title {
	flex-grow: 1;
	flex-shrink: 1;
	width: 500px;
	h2 {
		font-size: 38px;
		font-family: "Gotham Pro";
		color: $white;
		line-height: 1.2;
	}
}

.contacts__data {
	display: flex;
	flex-direction: column;
	flex-grow: 1;
	flex-shrink: 1;
	width: 500px;
}

.data__row {
	display: flex;
	flex-direction: row;

}

.data__phone,
.data__mail ,
.data__address,
.data__time {
	flex: 1;
	margin-top: 10px;
	margin-bottom: 10px;
	margin-left: 15px;
	margin-right: 15px;
	span {
		color: $white;
	}
	a {
		color: $white !important;
	}
	img {
		margin-right: 10px;
	}
}

.data__address {
	display: flex;
	align-items: flex-start;
}

footer {
	display: flex;
	justify-content: center;
	background-color: #4A45BE;
	border-top: 1px solid rgb(83, 96, 146);

	p {
		font-size: 12px;
		margin-top: 10px;
		margin-bottom: 10px;
		color: #6766BE;
	}
}

.btn-up {
	position: fixed;
	bottom: 20px;
	right: 20px;
	width: 40px;
	height: 40px;
	z-index: 500;
	background-color: #2B286D;
	border-radius: 50%;
	cursor: pointer;
	transition: 0.3s;
	img {
		position: relative;
		left: 11px;
		top: 10px;
		height: 20px;
	}
	&:hover {
		background-color: rgb(87, 82, 231);
	}
}

// ----------




//Adaptive version

@media (max-width: 1600px) {

	.footer__container {
		display: flex;
		flex-direction: column;
	}

	.contacts__container {
		flex: 1;
	}

	.contacts__second {
		justify-content: flex-start;
	}



}

@media (max-width: 1024px) {




}

@media (max-width: 992px) {
	
	.footer__contacts {
		flex-direction: column;
		align-items: center;
	}

	.contacts__title {
		text-align: center;
		margin-bottom: 20px;
	}

}

@media (max-width: 768px) {



}

@media (max-width: 640px) {

	.contacts__title,
	.contacts__data {
		width: auto;
	}

}

@media (max-width: 480px) {

	.contacts__title {
		h2 {
			font-size: 22px;
		}
	}

	.contacts__data {
		flex-direction: column;
		padding-left: 0;
		width: 100%;
		padding-right: 0;
	}

	.data__phone,
	.data__mail,
	.data__address,
	.data__time {
		margin-left: 0;
		margin-right: 0;
	}

	.data__row {
		flex-direction: column;
	}

	.btn-up {
		bottom: 10px;
		right: 15px;
	}

}

@media (max-width: 320px) {

}
