// Header banner

/* OFFER TITLE -----------*/

.banner__container {
	background-color: #2b286d;
	background-image: url('./../img/main-bg.jpg');
	background-repeat:  no-repeat;
	background-size: cover;
}

.banner-wrap {
	display: flex;
	flex-direction: row;
}

.main-offer {
	display: flex;
	flex-direction: column;
	flex: 1 1 66%;
	margin-right: 70px;
	color: $white;
}

.main-offer__block {
	margin-top: 80px;
	margin-bottom: 80px;
}

.main-offer__title-container {
	margin-bottom: 80px;
}

.offer-title {
	margin-bottom: 25px;
	h1 {
		font-size: 62px;
		font-family: "Gotham Pro";
		text-transform: uppercase;
		font-weight: 900;
		margin-bottom: 0;
	  }
	p {
		font-size: 38px;
		font-family: "Gotham Pro";
		font-weight: 900;
		margin-bottom: 0;
	}
	  
}

.offer-text {
	p {
		font-size: 20px;
		color: rgb(255, 255, 255);
		line-height: 1.4;
	}
}

.main-offer__plus {
	display: flex;
	padding: 40px 25px;
	border-radius: 10px;
	border: 1px solid #A09BC5;
}

.plus-item {
	position: relative;
	font-size: 20px;
	font-family: "Gotham Pro";
	font-weight: bold;
	padding-left: 30px;
	padding-top: 8px;
	padding-bottom: 8px;
	&::before {
		position: absolute;
		content: '';
		left: 0;
		top: 10px;
		width: 21px;
		height: 17px;
		background: url('./../img/ic-check.png') no-repeat;
	}
}

.plus__items-margin {
	margin-right: 30px;
}

// ----------




/* OFFER FORM -----------*/

.offer-form {
	display: flex;
	align-items: center;
	justify-content: center;
	flex: 1 1 34%;
	background-color: $blue;
	padding-left: 50px;
	padding-right: 50px;
}

.offer-form__container {
	margin-top: 50px;
	margin-bottom: 50px;
}

.offer-form__title {
	text-align: center;
	color: $white;
	margin-bottom: 30px;
	p {
		font-size: 26px;
		font-family: "Gotham Pro";
		line-height: 1.2;
		margin-bottom: 10px;
	}
}

.suptext__form-title {
	font-size: 16px;
	font-family: "Gotham Pro";
	font-weight: bold;
}

.subtext__form-title {
	font-size: 14px;
	line-height: 1.4;
}

.offer__input {
	transition-duration: 0.3s;
	& input:first-child {
		margin-bottom: 10px;
	}
	input {
		width: 100%;
		border-radius: 5px;
		height: 50px;
		padding-left: 40px;
	}
}

.name__input {
	position: relative;
	&::before {
		position: absolute;
		content: '';
		top: 19px;
		left: 15px;
		width: 10px;
		height: 11px;
		background: url('./../img/ic-form__name.png') no-repeat;
		opacity: 0.4;

	}

}

.phone__input {
	position: relative;
	&::before {
		position: absolute;
		content: '';
		top: 18px;
		left: 15px;
		width: 10px;
		height: 14px;
		background-color: red;
		background: url('./../img/ic-form__phone.png') no-repeat;
		opacity: 0.4;
	}
}

.main-btn {
	@include button($yellow);
	width: 100%;
	border: none;
	color: #061F32;
	font-size: 15px;
	font-family: "Gotham Pro";
	&:hover {
		color: $blue;
	}
}

// ----------




//Adaptive version

@media (max-width: 1400px) { 

	.main-offer__block {
		margin-top: 40px;
		margin-bottom: 40px;
	}

	.main-offer__title-container {
		margin-bottom: 50px;
	}

}

@media (max-width: 1024px) {
	.offer-form {
		padding-left: 20px;
		padding-right: 20px;
	}
}

@media (max-width: 992px) { 

	.banner-wrap {
		flex-direction: column;
	}

	.main-offer {
		margin-right: 0;
	}

	.main-offer__title-container {
		text-align: center;
	}

	.main-offer__plus {
		justify-content: center;
	}

}

@media (max-width: 768px) { //Epmty

	.main-offer__title-container {
		margin-bottom: 40px;
	}

	.offer-form {
		border-radius: 15px 15px 0 0;
	}

}

@media (max-width: 640px) { //Epmty

	.offer-title {
		margin-bottom: 15px;
		h1 {
			font-size: 46px;
		}
		p {
			font-size: 35px;
		}
	}

}

@media (max-width: 480px) { 

	.banner__container {
		background-image: none;
	}

	.main-offer__title-container {
		margin-bottom: 30px;
	}

	.offer-title {
		h1 {
			font-size: 26px;
		}
		p {
			font-size: 20px;
		}
	}

	.offer-text {
		p {
			font-size: 14px;
		}
	}

	.main-offer__plus {
		padding: 10px 5px;
	}

	.plus-item {
		font-size: 14px;
	}

	.plus__items-margin {
		margin-right: 0;
	}

	.plus__items {
		margin: 5px;
	}
	
	.offer-form {
		padding-left: 25px;
		padding-right: 25px;
	}


}

@media (max-width: 320px) {

	.plus-item {
			p {
				font-size: 12px;
			}
	}


	.offer-text {
		p {
			font-size: 13px;
		}
	}

	.offer-form__title {
		margin-bottom: 15px;
		p {
			font-size: 20px;
		}
	}

	.subtext__form-title {
		font-size: 12px;
	}

	.main-btn {
		font-size: 13px;
	}

}
