//CHOICE DESIGN

/*BLOCK -----------*/

.question {
	background-color: $blue;
}

.wrap-question {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	padding: 20px;
}

.question__title {
	margin-right: 40px;
}

.question__text {
	p {
		font-size: 26px;
		font-family: "Gotham Pro", sans-serif;
		color: $white;
		line-height: 1.2;
		font-weight: 900;
	}
}

.question__form {
	display: flex;
	flex-direction: row;
}

.question__input {
	position: relative;
	height: 50px;
	margin: 10px;
}

.question__btn {
	width: 260px;
	height: 50px;
	margin: 10px;
}

// ----------




//Adaptive version

@media (max-width: 1200px) {

	.wrap-question {
		flex-direction: column;
	}

	.question__title {
		text-align: center;
		margin-bottom: 10px;
		margin-right: 0;
	}


}

@media (max-width: 992px) {
	


}

@media (max-width: 768px) {

	.question__form {
		flex-direction: column;
		width: 100%;
	}

	.question__input {
		margin-left: 0;
		margin-right: 0;
	}

	.question__btn {
		width: 100%;
		margin-left: 0;
		margin-right: 0;
	}

}

@media (max-width: 640px) {



}

@media (max-width: 480px) {

	.question__text {
		p {
			font-size: 22px;
		}
	}


}

@media (max-width: 320px) {

