//STEP WORK


.title__two-row {
	flex-direction: column;
	text-align: center;
	h2 {
		margin-bottom: 15px;
	}
	p {
		font-size: 20px;
		line-height: 1;
	}
}

.steps-container {
	display: flex;
	flex-direction: row;
	justify-content: center;
	flex-wrap: wrap;
	margin-bottom: 80px;
}

.step-item {
	position: relative;
	overflow: hidden;
	box-sizing: border-box;
	width: 360px;
	height: 205px;
	margin: 10px;
	padding: 30px 20px;
	border-radius: 10px;
	background-color: #f6f9fc;
	img {
		margin-bottom: 10px;
	}
	p {
		font-size: 20px;
		font-family: "Gotham Pro";
		color: rgb(74, 69, 190);
		margin-bottom: 10px;
	}
	span {
		font-size: 13px;
	}
}

.step-item__one,
.step-item__two,
.step-item__three,
.step-item__four,
.step-item__five,
.step-item__six {
	&::after {
		position: absolute;
		top: 0;
		right: -20px;
		font-size: 180px;
		font-family: "Gotham Pro";
		color: rgba(54, 130, 255, 0.102);
		font-weight: 900;
	}
}

.step-item__one {
	&::after {
		content: '1';
	}
}

.step-item__two {
	&::after {
		content: '2';
		right: -40px;
	}
}
.step-item__three {
	&::after {
		content: '3';
		right: -40px;

	}
}
.step-item__four {
	&::after {
		content: '4';
		right: -35px;

	}
}
.step-item__five {
	&::after {
		content: '5';
		right: -40px;

	}
}
.step-item__six {
	&::after {
		content: '6';
		right: -40px;
	}
}

.stepwork__consult {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	background-color: $blue;
	padding: 30px 60px;
	border-radius: 10px;
}

.consult__left {
	display: flex;
	flex-direction: row;
}

.consult__left-img {
	position: relative;
	width: 140px;
	height: 40px;
	margin-right: 40px;
	img {
		position: absolute;
		bottom: -20px;

	}
}

.consult__left-text {
	margin-right: 20px;
	p {
		font-size: 20px;
		font-family: "Gotham Pro";
		color: $white;
		line-height: 1.2;
		font-weight: 600;
		margin-bottom: 8px;
		  
	} 
	span {
		font-size: 18px;
		color: $white;
		line-height: 1.3;
	}
}

.consult-btn {
	width: 315px;
}


// ----------




//Adaptive version

@media (max-width: 1400px) { //Empty
	
}

@media (max-width: 1200px) {

	.step-item {
		width: 45%;
		text-align: center;
	}
}


@media (max-width: 1024px) { //Empty



}

@media (max-width: 992px) { 
	
	.stepwork__consult {
		flex-direction: column;
	}

	.consult__left {
		margin-bottom: 20px;
	}

	.consult__right-btn {
		width: 100%;
	}

	.consult-btn {
		width: 100%;
	}


}

@media (max-width: 768px) {

	.step-item {
		width: 100%;
			
	}

	.consult__left {
		flex-direction: column;
		text-align: center;

	}

	.consult__left-img {
		width: 100%;
		margin-right: 0;
		img {
			left: 0;
			right: 0;
			bottom: 0;
			margin: auto;

		}
	}

	.consult__left-text {
		margin-right: 0;
	}

}

@media (max-width: 480px) {

	.stepwork__consult {
		padding-left: 30px;
		padding-right: 30px;
	}

	.title__two-row {
		p {
			font-size: 16px;
		}
	}

}

@media (max-width: 320px) { //Empty

}
