//CHOICE DESIGN

/*BLOCK -----------*/

.choice-title {
	justify-content: flex-start;
}

.choice__aboutus {
	display: flex;
	flex-direction: row;
	align-items: flex-start;

}

.aboutus__left {
	position: relative;
	border: 5px solid rgb(233, 239, 245);
	border-radius: 10px;
	padding: 35px 125px 35px 45px;
	&:before {
		position: absolute;
		bottom: 35px;
		right: 60px;
		content: '';
		background: url('./../img/ic-quote.png');
		width: 50px;
		height: 35px;
	}
	  
}

.aboutus__left-text {
	margin-bottom: 20px;
	p {
		font-size: 20px;
		line-height: 1.4;
		width: 405px;
	}
}

.choice-btn {
	width: 316px;
}

.aboutus__right {
	position: relative;
	width: 568px;
	height: 240px;
}

.aboutus__img-container {
	position: absolute;
	right: 20px;
	top: -40px;

}



// ----------




//Adaptive version

@media (max-width: 1024px) {

	.choice__aboutus {
		flex-direction: column;
	}

	.aboutus__left {
		padding-right: 45px;
		width: 100%;
		text-align: center;
		&::before {
			right: 30px;
		}
	}

	.aboutus__left-text {
		p {
			width: 100%;
			font-size: 24px;
		}
	}

	.aboutus__right {
		display: none;
	}

	.choice-title {
		justify-content: center;
		h2 {
			text-align: center;
		}
	}

	.aboutus__left-btn {
		display: flex;
		justify-content: center;
	}


}

