//CERTIFICATE

/*BLOCK -----------*/

.certificate__block {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: center;
}

.certificate__left,
.certificate__right {
	display: flex;
}

.certificate {
	flex: 1 1 262px;
	height: auto;
	margin: 15px;

}




// ----------




//Adaptive version

@media (max-width: 1024px) {

	.review__item {
		&::after {
			display: none;
		}
	}


}

@media (max-width: 992px) {
	
	.review__block {
		flex-direction: column;
	}


}

@media (max-width: 768px) {



}

@media (max-width: 640px) {

	.certificate {
		img {
			width: 100px;
		}
	}

}

@media (max-width: 480px) {

	.review__item {
		padding-left: 30px;
		padding-right: 30px;
	}

	.review__text {
		margin-bottom: 20px;
		p {
			text-align: center;
		}
	}

	.person__avatar {
		margin-right: 0;
		margin-bottom: 15px;
	}

	.review__person {
		flex-direction: column;
		justify-content: center;
		text-emphasis: center;
	}

	.person__name {
		margin-right: 0;
		margin-bottom: 15px;
	}

}

@media (max-width: 320px) {

}
