//OUR WORK

/*Work -----------*/

.work__grid {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
}

.our-work__container {
	display: flex;
	flex-direction: row;
	justify-content: center;
}

.our-work__btns {
	display: flex;
	flex-direction: row;
}

.our-work__btn {
	padding-top: 5px;
	padding-left: 15px;
	padding-bottom: 20px;
	padding-right: 15px;
	border-bottom: 2px solid transparent;
	cursor: pointer;
	span {
		text-transform: uppercase;
		color: $blue;
	}
	&:hover {
		border-bottom: 2px solid $blue;
	}
}

.work__img {
	overflow: hidden;
	box-sizing: border-box;
	border: 4px solid transparent;
	transition-duration: 0.3s;
	flex: 1 0 250px;
	height: 300px;

	a {
		display: inline-block;
	}
	&:hover {
		border: 4px solid $yellow;
	}
}

