
.main-title {
	display: flex;
	justify-content: center;
	margin-bottom: 30px;
	h2 {
		font-size: 38px;
		font-family: "Gotham Pro";
		font-weight: 900;
		color: $dark-blue;
	}
}

.advantages__container {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
}

.advantage__item {
	flex: 1 1 250px;
	padding: 30px 25px;
	margin: 10px;
	border-radius: 10px;
	box-shadow: 0px 0 25px rgb(231, 230, 255);
}

.advantage__title {
	display: flex;
	flex-direction: row;
	align-items: center;
	margin-bottom: 15px;
	p {
		font-size: 18px;
		font-family: "Gotham Pro";
		color: $dark-blue;
		font-weight: bold;
		line-height: 1.2;
	}
}

.advantage__img-box {
	width: 35px;
	height: 35px;
	img {
		padding-top: 3px;
	}
	margin-right: 15px;
}

.advantage__text {
	p {
		font-size: 14px;
		font-family: "Roboto";
		line-height: 1.429;
	}
}




