//HEADER DESIGN

/*FIRST HEADER -----------*/

//Header Scroll

.header-scroll {
	position: fixed;
	z-index: 100;
	width: 100%;
	background-color: white;
	box-shadow: 0 2px 15px #E6E6E6;
}

.section-padding {
	padding-top: 120px;
}

.header-first {
	padding-top: 4px;
	padding-bottom: 4px;
	border-bottom: 1px solid #E6E6E7;
	background-color: $white;
}

.first-header__wrap {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
}

.header-first__description {
	flex: 2 2 50%;
	span {
		font-size: 13px;
		font-family: "Roboto";
		color: rgb(77, 94, 115);
		line-height: 0.9;
	}
}

.header-first__contacts {
	display: flex;
	flex-direction: row;
	justify-content: flex-end;
	align-items: center;
	flex: 1 1 50%;
	& div:last-child {
		margin-right: 0;
	}	
}

.contacts-header__time {
	margin-right: 25px;
	span {
		font-size: 13px;
		font-family: "Roboto";
		color: rgb(128, 135, 144);
	}
}

.contacts-header__mail {
	margin-right: 25px;
	a {
		font-size: 13px;
		font-family: "Roboto";
		color: rgb(6, 31, 50);
		&:hover {
			color: $blue;
			text-decoration: none;
		}
	}
}

.contacts-header__phone {
	margin-right: 25px;
	a {
		font-size: 16;
		font-family: "Gotham Pro";
		color: rgb(5, 13, 22);
	  }
}

// ----------




/*SECOND HEADER -----------*/

.header__container {
	padding-top: 20px;
	padding-bottom: 20px;
	background-color: $white;
}

.main-header__wrap {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.header-second__logo {
	flex: 2 2 10%;
}

.header-second__menu {
	justify-content: flex-end;
	align-items: center;
	flex-wrap: wrap;
	flex: 1 1 90%;
}

.header-second__main-menu {
	margin-bottom: 10px;
}

.header-second__main-menu,
.header-second__under-menu {
	display: flex;
	justify-content: flex-end;
	ul {
		display: flex;
		padding-left: 0;
		margin-bottom: 0;
		& li:last-child {
			padding-right: 0;
		}
	}
}

.main-menu__item {
	list-style: none;
	padding-left: 20px;
	padding-right: 20px;
	a {
		font-size: 16px;
		font-family: "Gotham Pro";
		color: rgb(5, 13, 22);
		font-weight: bold;
		text-transform: uppercase;
		&:hover {
			position: relative;
			text-decoration: none;
			color: $blue;
		}
	}
}

.under-menu__item {
	list-style: none;
	padding-left: 12px;
	padding-right: 12px;
	a {
		font-size: 13px;
		font-family: "Gotham Pro";
		color: rgb(116, 128, 142);
		text-transform: uppercase;
		&:hover {
			text-decoration: none;
			color: $blue;
		}
	}
}

// ----------




//Adaptive version

@media (max-width: 1400px) { //Empty
	
}

@media (max-width: 1024px) {

	.header-second__menu {
		margin-right: 20px;
	}

	.header-second__main-menu {
		margin-bottom: 0;
	}
	
	.main-menu__item {
		padding: 10px;
	}
	
	.header-second__under-menu {
		display: none;
	}
}

@media (max-width: 992px) { 
	
	.header__container { 
		padding: 0;
	}

	.main-menu__item a {
		font-size: 14px;
	}

}

@media (max-width: 768px) { 

	.header-first {
		padding-top: 8px;
		padding-bottom: 8px;
	}

	.header-first__contacts {
		justify-content: center;
	}

	.contacts-header__time span,
	.contacts-header__mail a {
		font-size: 16px;
	}

	.contacts-header__phone a {
		font-size: 20px;
	}

	.header-first__description {
		display: none;
	}

	.header-second__main-menu {
		display: none;
	}

}

@media (max-width: 640px) { 

	.header-first {
		display: none;
	}

}

@media (max-width: 480px) { //Empty



}

@media (max-width: 320px) { //Empty

}
